import React from 'react';
import { Link } from 'react-router-dom';

const ArticleList = ({ articles }) => (
    <>
        {articles.map((artcle, key) => (
            <Link key={key} to={`/article/${artcle.name}`}>
                <h3>{artcle.title}</h3>
                <p>{artcle.content[0].substring(0, 150)}...</p>
            </Link>
        ))}
    </>
);

export default ArticleList;