import React, { useState, useEffect } from 'react';
import ArticlesList from '../contents/ArticlesList';
import CommentsList from '../contents/CommentsList';
import UpvoteSection from '../contents/UpvoteSection';
import articleContents from '../contents/ArticleContents';
import NotFoundPage from '../contents/NotFoundPage';
import AddCommentForm from '../contents/AddCommentForm';

const ArticlePage = ({ match }) => {

    const name = match.params.name;
    const Article = articleContents.find(article => article.name === name);

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

    useEffect(() => {
        const fetchData = async () =>{
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArticleInfo(body);
        }
        fetchData();
    }, [name]);

    if (!Article) return <NotFoundPage />

    const otherArticles = articleContents.filter(article => article.name !== Article.name);
    return (
        <>
            <h1>{Article.title}</h1>
            <UpvoteSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo}/>
            <p>{Article.content.map((paragraph, key) => (
                <p key={key}>{paragraph}</p>
            ))}</p>
            <AddCommentForm articleName={name} setArticleInfo={setArticleInfo}/>
            <CommentsList comments={articleInfo.comments}/>
            <h3>Other Articles:</h3>
            <ArticlesList articles={otherArticles} />
        </>
    );
}

export default ArticlePage;